.Hots .scroll-con {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  display: -ms-flexbox;
  display: flex;
}
.Hots .list-view-section-body {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  display: -ms-flexbox;
  display: flex;
}
.Hots .am-list-header {
  padding: 0;
  width: 100%;
  margin-bottom: 10px;
}
.Hots .am-carousel {
  height: 100px;
}
.Hots.base-content {
  height: calc(100vh - 88.5px);
}
.Hots .slider-decorator-0 {
  opacity: 0;
  padding-right: 15px;
  bottom: 10px !important;
}
.Hots .slider-decorator-0 .am-carousel-wrap {
  text-align: right;
}
.Hots .anchor-recommend {
  padding: 15px 0;
  background: #fff;
  -webkit-box-shadow: inset 0 0 0 0 #f3f3f3;
  text-align: center;
}
.Hots .anchor-recommend span {
  position: relative;
  font-size: 13px;
  color: #666;
}
.Hots .anchor-recommend span::before,
.Hots .anchor-recommend span::after {
  content: "";
  width: 16px;
  height: 1px;
  background: #b6b6b6;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
      transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
}
.Hots .anchor-recommend span::before {
  left: -21px;
}
.Hots .anchor-recommend span::after {
  right: -21px;
}



