.AddressManage .base-content {
  height: calc(100vh - 108px);
}
.AddressManage .right-label {
  font-size: 16px;
  color: #333;
}
.AddressManage .am-radio {
  margin-right: 10px;
  padding: 2.5px;
  border: 1px solid #ccc;
  border-radius: 50%;
}
.AddressManage .am-radio.am-radio-checked {
  border: 1px solid #e21b1a;
  background: #e21b1a;
}
.AddressManage .am-radio.am-radio-checked .am-radio-inner::after {
  width: 5px;
  height: 10px;
  top: -1px;
  right: 7px;
  border-color: #fff;
}
.AddressManage .am-list-item .am-list-content {
  font-size: 14px;
  color: #181818;
}
.AddressManage .am-list-item .am-list-content span {
  float: right;
}
.AddressManage .am-list-item .am-list-content .am-list-brief {
  line-height: 18px;
  white-space: initial;
  font-size: 12px;
  color: #666;
}
.AddressManage .addrOpera {
  padding: 15px;
  background: #fff;
  font-size: 13px;
}
.AddressManage .addrOpera .defaultAddr {
  color: #e21b1a;
}
.AddressManage .addrOpera .addrEdit {
  text-align: right;
  color: #333;
}
.AddressManage .addrOpera .spa {
  margin: 0 8px;
}



