.OrderDetail .base-content {
  height: calc(100vh - 95px);
}
.OrderDetail .base-content .order-item {
  background: #fff;
}
.OrderDetail .base-content .flex-item {
  padding: 15px 15px 0;
}
.OrderDetail .base-content .flex-item:last-of-type {
  padding-bottom: 15px;
}
.OrderDetail .base-content .flex-item .labelItem {
  font-size: 13px;
  color: #b0b0b0;
}
.OrderDetail .base-content .flex-item .labelItem.orderCon {
  font-size: 14px;
}
.OrderDetail .base-content .flex-item .labelItem.orderPrice {
  color: #e21b1a;
}
.OrderDetail .base-content .flex-item.orderTit {
  font-size: 16px;
  color: #e21b1a;
}
.OrderDetail .base-content .order-address {
  padding: 15px;
}
.OrderDetail .base-content .order-address .addr-icon {
  margin-right: 20px;
}
.OrderDetail .base-content .order-address .addr-icon .addrIco {
  margin-top: -15px;
  height: 20px;
}
.OrderDetail .base-content .order-address .addr-info {
  line-height: 16px;
  font-size: 14px;
}
.OrderDetail .base-content .order-address .addr-info .addr-ess {
  margin-top: 10px;
  line-height: 20px;
  font-size: 13px;
  color: #333;
}
.OrderDetail .base-content .goods-info {
  padding: 10px;
}
.OrderDetail .base-content .goods-info .goods-img {
  width: 75px;
  height: 75px;
  margin-right: 2px;
}
.OrderDetail .base-content .goods-info .title {
  padding-top: 5px;
  line-height: 20px;
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 14px;
  color: #333;
}
.OrderDetail .base-content .goods-info .bottom-info {
  margin-top: 15px;
}
.OrderDetail .base-content .goods-info .bottom-info .spec {
  font-size: 14px;
  color: #999;
  height: 22px;
  line-height: 22px;
}
.OrderDetail .base-content .goods-info .bottom-info .spec span {
  margin-right: 8px;
}
.OrderDetail .base-content .goods-info .goods-refurn {
  margin-top: 8px;
}
.OrderDetail .base-content .goods-info .goods-refurn .am-button {
  border-radius: 2px;
  color: #333;
}
.OrderDetail .base-content .goods-info .goods-refurn .am-button::before {
  border: 1px solid #f5f5f5;
}
.OrderDetail .base-content .goods-info .price {
  padding-top: 8px;
  font-size: 13px;
  font-weight: 600;
  text-align: right;
  color: #333;
}
.OrderDetail .footer {
  padding: 0 15px;
  height: 49px;
  background: #fff;
  border-top: 1px solid #f1f1f1;
}
.OrderDetail .footer .am-button-ghost {
  margin-left: 15px;
  border-radius: 2px;
  color: #000;
}
.OrderDetail .footer .am-button-ghost::before {
  border: 1px solid #f5f5f5;
  border-radius: 2px;
}
.OrderDetail .footer .eva-order {
  color: #e21b1a;
}
.OrderDetail .footer .eva-order::before {
  border: 1px solid #e21b1a;
  border-radius: 2px;
}



