.ConfirmOrder .base-content {
  height: calc(100vh - 100px);
}
.ConfirmOrder .base-content .img-line {
  width: 100%;
  height: 5px;
  display: block;
  vertical-align: top;
}
.ConfirmOrder .base-content .order-address {
  padding: 20px 15px;
  background: #fff;
}
.ConfirmOrder .base-content .order-address .addr-info {
  font-size: 14px;
  color: #333;
}
.ConfirmOrder .base-content .order-address .addr-info .addr-ess {
  margin-top: 10px;
  line-height: 20px;
  font-size: 14px;
}
.ConfirmOrder .base-content .order-address .addr-info .addr-user {
  font-size: 14px;
  color: #000;
  font-weight: 500;
}
.ConfirmOrder .base-content .order-address .add-addr {
  text-align: center;
}
.ConfirmOrder .base-content .order-address .add-addr .addr-img {
  width: 60px;
  height: 60px;
  line-height: 70px;
  display: inline-block;
  background: url("../../images/addr/addRessBg.png");
  background-size: contain;
  background-position: center;
}
.ConfirmOrder .base-content .order-address .add-addr .addr-img .addIco-img {
  vertical-align: bottom;
  height: 35px;
}
.ConfirmOrder .base-content .order-address .add-addr .add-tips {
  margin-top: 24px;
  line-height: 18px;
  font-size: 13px;
  color: #333;
}
.ConfirmOrder .base-content .order-address .add-addr .add-tips em {
  color: #e21b1a;
}
.ConfirmOrder .base-content .store-info {
  padding: 10px 15px;
  background: #fff;
}
.ConfirmOrder .base-content .store-info img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
}
.ConfirmOrder .base-content .store-info .store-name {
  font-size: 14px;
}
.ConfirmOrder .base-content .buy-num {
  padding: 13px 15px;
  background: #fff;
  font-size: 13px;
  color: #333;
}
.ConfirmOrder .base-content .buy-num .am-stepper {
  width: 100px;
  height: 22px;
  line-height: 22px;
  padding: 0;
}
.ConfirmOrder .base-content .buy-num .am-stepper-input-wrap {
  height: 22px;
  line-height: 22px;
}
.ConfirmOrder .base-content .buy-num .am-stepper-input-wrap .am-stepper-input {
  width: 30px;
}
.ConfirmOrder .base-content .buy-num .am-stepper-handler {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  line-height: 20px;
}
.ConfirmOrder .base-content .buy-num .am-stepper-handler-up svg {
  fill: #333;
}
.ConfirmOrder .base-content .discount-item {
  padding: 13px 15px;
  background: #fff;
}
.ConfirmOrder .base-content .discount-item .am-flexbox-item {
  font-size: 13px;
}
.ConfirmOrder .base-content .discount-item .am-flexbox-item em {
  margin-left: 8px;
  color: #999;
}
.ConfirmOrder .base-content .discount-item .am-flexbox-item:last-of-type {
  font-size: 15px;
  text-align: right;
  color: #e21b1a;
}
.ConfirmOrder .base-content .discount-item .am-flexbox-item .am-list-line {
  padding-right: 0;
}
.ConfirmOrder .base-content .discount-item .am-flexbox-item .am-list-line input {
  text-align: right;
}
.ConfirmOrder .base-content .discount-item .am-radio {
  margin-left: 8px;
  width: 20px;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 50%;
}
.ConfirmOrder .base-content .discount-item .am-radio .am-radio-inner {
  width: 20px;
  height: 20px;
  border-width: 1px;
}
.ConfirmOrder .base-content .discount-item .am-radio.am-radio-checked .am-radio-inner::after {
  width: 10px;
  height: 10px;
  background: #e21b1a;
  border-radius: 50%;
  top: 50% !important;
  right: 50% !important;
  border-color: #e21b1a;
  border-width: inherit;
  -webkit-transform: translate(50%, -50%);
      -ms-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
}
.ConfirmOrder .base-content .discount-item .am-list-item {
  padding-left: 0;
  height: auto;
  min-height: auto;
  -ms-flex: 1 1;
      flex: 1 1;
}
.ConfirmOrder .base-content .discount-item .am-list-item .am-list-line {
  padding-left: 0;
}
.ConfirmOrder .base-content .discount-item .am-list-item .am-input-label {
  font-size: 13px;
  color: #333;
}
.ConfirmOrder .base-content .discount-item .am-list-item input {
  padding: 1px 0;
  font-size: 14px;
}
.ConfirmOrder .base-content .discount-item .am-list-item ::-webkit-input-placeholder {
  font-size: 14px;
  color: #e0e0e0;
}
.ConfirmOrder .base-content .discount-item .am-checkbox-wrapper {
  margin-left: 8px;
}
.ConfirmOrder .base-content .order-invoice {
  font-size: 13px;
  color: #333;
}
.ConfirmOrder .base-content .order-invoice-check span {
  margin-left: 15px;
}
.ConfirmOrder .base-content .order-remark {
  padding: 10px;
  background: #fff;
}
.ConfirmOrder .base-content .order-remark .am-textarea-item {
  border-radius: 4px;
  background: #f0f0f0;
  font-size: 12px;
  color: #999;
}
.ConfirmOrder .base-content .order-remark textarea {
  height: 50px;
  font-size: 13px;
}
.ConfirmOrder .base-content .order-remark ::-webkit-input-placeholder {
  font-size: 12px;
  color: #999;
}
.ConfirmOrder .footer {
  height: 54px;
  background: #fff;
  padding: 0 15px;
  color: #666;
  border-top: 1px solid #eee;
}
.ConfirmOrder .footer .goodsNum {
  font-size: 12px;
  color: #999;
  margin-bottom: 3px;
}
.ConfirmOrder .footer .total {
  font-size: 12px;
  color: #333;
}
.ConfirmOrder .footer .total em {
  font-size: 16px;
  color: #e21b1a;
}
.ConfirmOrder .footer .pay-btn {
  background: #e21b1a;
  color: #fff;
  height: 36px;
  line-height: 36px;
  font-size: 14px;
}



