.MyProduct .list-view-section-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-top: 10px;
}
.MyProduct .list-view-section-body .recommend-goodsItem {
  position: relative;
}
.MyProduct .list-view-section-body .recommend-goodsItem .recommend-goodsItem-body {
  padding: 10px 10px;
}
.MyProduct .list-view-section-body .recommend-goodsItem .productEdit,
.MyProduct .list-view-section-body .recommend-goodsItem .productAbled {
  width: 50px;
  height: 28px;
  line-height: 28px;
  border-radius: 4px;
  text-align: center;
  font-size: 12px;
  position: absolute;
  left: 10px;
  top: 10px;
}
.MyProduct .list-view-section-body .recommend-goodsItem .goodsPrice {
  font-size: 13px;
}
.MyProduct .list-view-section-body .recommend-goodsItem .productEdit {
  border: 1px solid #e21b1a;
  color: #e21b1a;
}
.MyProduct .list-view-section-body .recommend-goodsItem .productAbled {
  border: 1px solid #c4c4c4;
  background: #c4c4c4;
  color: #fff;
}
.MyProduct .list-view-section-body .recommend-goodsItem .item-btn {
  height: 24px;
  line-height: 24px;
  padding: 0 5px;
  margin-left: 10px;
}



