::-webkit-input-placeholder {
  text-align: center;
  font-size: 14px;
}
.ActivifyShare {
  min-height: 100%;
  -webkit-transform: inherit !important;
      -ms-transform: inherit !important;
          transform: inherit !important;
}
.ActivifyShare .base-content {
  height: 100vh;
}
.ActivifyShare .countDown {
