  padding: 2px;
  border-radius: 2px;
  background: #e31b19;
  color: #fff;
}
.ActivifyShare .countDown.nobg {
  background: transparent;
  color: #e31b19;
}
.ActivifyShare .voteBox .am-list-line::after {
  height: 0 !important;
}
.ActivifyShare .share_box {
  background: url("../../images/activify/activify_01.jpg") no-repeat;
  background-size: 100% 100%;
  text-align: center;
}
.ActivifyShare .share_box_brank {
  padding-top: 55px;
  margin-bottom: 48px;
  font-size: 20px;
  color: #e31b19;
}
.ActivifyShare .share_box_user {
  width: 60%;
  margin-left: 20%;
}
.ActivifyShare .share_box_user .user_box .user_img {
  margin-right: 21px;
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.ActivifyShare .share_box_user .user_box_info {
  font-size: 16px;
  color: #222;
}
.ActivifyShare .share_box_user .user_box_info img {
