}
.ActivifyShare .share_box_user .u_img {
  margin: 20px 0;
  width: 180px;
}
.ActivifyShare .title_type {
  height: 25px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.ActivifyShare .title_type.one {
  background-image: url("../../images/activify/activify_02.jpg");
}
.ActivifyShare .title_type.two {
  height: 23px;
  background-image: url("../../images/activify/activify_04.jpg");
}
.ActivifyShare .vote_box {
  background-image: url("../../images/activify/activify_03.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.ActivifyShare .vote_box.intro {
  background-image: url("../../images/activify/activify_05.jpg");
}
.ActivifyShare .vote_box.goods {
