  padding-bottom: 16px;
  text-align: center;
}
.ActivifyShare .voteBox .voteInput {
  margin: auto;
  width: 230px;
  height: 40px;
  min-height: 40px;
  border-radius: 30px;
  font-size: 14px;
}
