  width: 230px;
  min-height: 40px;
  line-height: 40px;
  border: none;
  border-radius: 30px;
  background: #e31b19;
  font-size: 16px;
  color: #fff;
}
.ActivifyShare .voteBox .voteBtn.disabled {
