  background: #fff;
  border-radius: 5px;
  text-align: center;
}
.ActivifyShare .story_box .totalNumber .total_box .title {
  font-size: 12px;
  color: #333;
}
.ActivifyShare .story_box .totalNumber .total_box .number {
  margin-top: 16px;
  font-size: 16px;
  color: #e31b19;
  font-weight: 600;
}
.ActivifyShare .goods_box {
  margin-left: 10%;
  width: 80%;
  background: #fff;
}
.ActivifyShare .vote_prize {
  padding: 19px 0;
  border-bottom: 1px solid #f1f1f1;
  text-align: center;
  font-size: 14px;
  color: #e31b19;
}
.ActivifyShare .goods_box_item {
  padding: 10px;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.ActivifyShare .goods_box_item .goods-item {
  margin-right: 10px;
}
.ActivifyShare .goods_box_item .prizeImg {
  height: 100px;
  max-width: 100px;
}
.ActivifyShare .goods_box_item .goodsTit {
