}
.ActivifyShare .my-list .am-list-item .am-list-line .am-list-extra {
  font-size: 12px;
  color: #666;
  -o-text-overflow: initial;
     text-overflow: initial;
  white-space: inherit;
}
.ActivifyShare .footer {
  width: 100%;
  height: 51px;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 99;
}
.ActivifyShare .footer .footerItem {
  margin: 0;
  width: 50%;
  height: 51px;
  line-height: 51px;
  background: #e31b19;
  text-align: center;
  font-size: 16px;
  color: #fff;
}
.ActivifyShare .footer .footerItem.issueBtn {
  background: #fff;
  color: #e31b19;
}



