.MessageCenter .msgList .title {
  font-size: 14px;
  color: #333;
}
.MessageCenter .msgList .title.read {
  color: #666;
}
.MessageCenter .msgList .time,
.MessageCenter .msgList .content {
  margin-top: 8px;
  font-size: 12px;
  color: #999;
}
.MessageCenter .msgList .time p,
.MessageCenter .msgList .content p {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.MessageCenter .msgList .am-list-brief {
  font-size: 12px;
  color: #999;
}
.MessageCenter .msgList .am-list-item {
  padding-left: 0;
}
.MessageCenter .msgList .am-list-item .am-list-line-multiple {
  padding-left: 15px;
}



