.H5Live {
  position: relative;
  height: 100vh;
}
.H5Live .bg-con {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-size: 100% 100%;
  font-size: 14px;
  color: #fff;
  text-align: center;
  padding-top: 50%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.H5Live .top-con {
  position: absolute;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 2;
  padding: 10px;
  top: 0;
  left: 0;
  width: 100%;
  color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.H5Live .top-con .left-con {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  padding-right: 15px;
}
.H5Live .top-con .left-con .header {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}
.H5Live .top-con .left-con .name {
  font-size: 11px;
  margin-bottom: 3px;
}
.H5Live .top-con .left-con .view {
  font-size: 9px;
}
.H5Live .top-con .down-con {
  background: #fccd12;
  padding: 3px 10px;
  font-size: 11px;
  color: #333;
  border-radius: 10px;
}
.H5Live .top-con .room-id-con {
  font-size: 11px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 4px 10px;
  margin-top: 10px;
  display: inline-block;
}
.H5Live .bag-con {
  position: absolute;
  right: 10px;
  top: 30%;
  font-size: 10px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  width: 54.4px;
  height: 61.6px;
  color: #fff;
  text-align: center;
  z-index: 100;
}
.H5Live .bag-con div {
  padding-bottom: 2px;
}
.H5Live .bag-con .bottom-bag {
  padding-bottom: 5px;
}
.H5Live .bottom-con {
  background: transparent;
  position: absolute;
  z-index: 2;
  padding: 10px;
  left: 0;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  bottom: 0px;
}
.H5Live .bottom-con .scroll-con {
  overflow-y: auto;
  max-height: 140px;
  width: 60%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-bottom: 10px;
}
.H5Live .bottom-con .scroll-con .vip {
  width: 8px;
  height: 8px;
  margin-right: 5px;
  vertical-align: baseline;
}
.H5Live .bottom-con .scroll-con .vip-con {
  margin-right: 5px;
  background: #e75953;
  border-radius: 10px;
  padding: 1px 8px;
  font-size: 11px;
  color: #fff;
  display: inline-block;
  line-height: 11px;
}
.H5Live .bottom-con .tips {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: rgba(0, 0, 0, 0.4);
  font-size: 11px;
  line-height: 18px;
  color: #e75953;
  padding: 5px;
  width: 100%;
  border-radius: 5px;
  margin-bottom: 10px;
  padding-left: 10px;
}
.H5Live .bottom-con .info-con {
  width: 100%;
  padding: 5px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.7);
}
.H5Live .bottom-con .info-con .label {
  font-size: 13px;
  padding-left: 5px;
}
.H5Live .bottom-con .info-con .invite-con {
  background: #ffcb02;
  border-radius: 15px;
  padding: 5px 20px;
}
.H5Live .bottom-con .left-con,
.H5Live .bottom-con .right-con {
  padding: 0 10px;
}
.H5Live .bottom-con .left-con img,
.H5Live .bottom-con .right-con img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.H5Live .bottom-con .left-con img {
  margin-right: 10px;
}
.H5Live .bottom-con .right-con img {
  margin-left: 10px;
}
.H5Live .vcp-error-tips {
  font-size: 13px;
  color: #fff;
}
.H5Live .carousel-con {
  position: absolute;
  top: 85px;
  width: 100%;
  height: 200px;
  z-index: 2;
}



