.Statistics .list-view-section-body {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  display: -ms-flexbox;
  display: flex;
}
.Statistics .am-list-body {
  background: transparent;
}
.Statistics .am-list-body:after {
  height: 0 !important;
}
.Statistics .intBox {
  padding: 15px;
}
.Statistics .intBox .myInt {
  padding: 15px 0;
  background: #fff;
  border-radius: 8px;
  text-align: center;
}
.Statistics .intBox .myInt .round {
  margin: auto;
  line-height: 85px;
  width: 85px;
  height: 85px;
  border-radius: 50%;
  border: 4px solid #f70692;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 24px;
}
.Statistics .intBox .myInt .label {
  margin-top: 10px;
  font-size: 13px;
}



