.EvaluateList .item {
  background: #fff;
  border-bottom: 1px solid #eee;
  padding: 15px;
}
.EvaluateList .item .userImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}
.EvaluateList .item .name {
  font-size: 14px;
  color: #333;
  margin-bottom: 10px;
}
.EvaluateList .item .des {
  font-size: 14px;
  color: #333;
}
.EvaluateList .item .photos {
  margin-top: 8px;
}
.EvaluateList .item .desImg {
  margin: 0 8px 8px 0;
  width: 80px;
}



