.MessageDetail {
  background: #fff;
}
.MessageDetail .msgBox {
  padding: 20px 15px;
}
.MessageDetail .msgBox .msgTit {
  line-height: 14px;
  text-align: center;
  font-size: 16px;
  color: #333;
}
.MessageDetail .msgBox .msgCont {
  margin-top: 10px;
  line-height: 20px;
  text-indent: 2em;
  font-size: 13px;
  color: #666;
}
.MessageDetail .msgBox .msgCont img {
  width: 100%;
  display: block;
}



