.OrderGoodsItem.goods-info {
  padding: 10px;
  background: #fff;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.OrderGoodsItem.goods-info .goods-img {
  width: 75px;
  height: 75px;
  margin-right: 2px;
}
.OrderGoodsItem.goods-info .title {
  padding-top: 5px;
  line-height: 20px;
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 14px;
  color: #333;
}
.OrderGoodsItem.goods-info .bottom-info {
  margin-top: 15px;
}
.OrderGoodsItem.goods-info .bottom-info .spec {
  font-size: 14px;
  color: #999;
  height: 22px;
  line-height: 22px;
}
.OrderGoodsItem.goods-info .price {
  padding-top: 8px;
  font-size: 13px;
  font-weight: 600;
  text-align: right;
  color: #333;
}



