.PayState {
  background: #fff;
}
.PayState-box {
  padding: 12px 0;
  -webkit-box-shadow: inset 0 0 0 0 #F0F0F0;
          box-shadow: inset 0 0 0 0 #F0F0F0;
  text-align: center;
}
.PayState-box img {
  height: 60px;
}
.PayState-box-tips {
  margin-top: 12px;
  font-size: 16px;
  color: #e21b1a;
}
.PayState-order {
  border-top: 1px solid #f1f1f1;
  border-bottom: 1px solid #f1f1f1;
  padding: 20px;
}
.PayState-order .orderItem {
  line-height: 16px;
  letter-spacing: 0;
  font-size: 13px;
  color: #999;
}
.PayState-order .orderItem:nth-child(2) {
  margin: 10px 0;
}
.PayState-order .orderItem span {
  color: #151515;
}
.PayState-opear {
  height: 110px;
}
.PayState-opear .am-button {
  height: 36px;
  line-height: 36px;
  font-size: 14px;
}
.PayState-opear .am-button.payBtn {
  margin-right: 80px;
  border: 1px solid #e21b1a;
  color: #e21b1a;
}
.PayState-opear .am-button.payBtn::before {
  border: none;
}



