.MyOrder .orderItem {
  margin-bottom: 8px;
  background: #fff;
}
.MyOrder .orderItemTit {
  padding: 11px 15px;
}
.MyOrder .orderItemTit span {
  font-size: 12px;
  color: #999;
}
.MyOrder .orderItemTit span:last-of-type {
  color: #e21b1a;
}
.MyOrder .orderItemType {
  padding: 8px 15px;
  background: #fff;
}
.MyOrder .orderItemType .totalItem {
  padding: 8px 0;
  text-align: right;
  font-size: 12px;
}
.MyOrder .orderItemType .totalItem .priceTotal {
  color: #e21b1a;
}
.MyOrder .orderItemType .am-button-ghost {
  border-radius: 2px;
  color: #000;
}
.MyOrder .orderItemType .am-button-ghost::before {
  border: 1px solid #f5f5f5;
  border-radius: 2px;
}
.MyOrder .orderItemType .am-button-borderfix,
.MyOrder .orderItemType .eva-order {
  margin-left: 10px;
}
.MyOrder .orderItemType .eva-order {
  color: #e21b1a;
}
.MyOrder .orderItemType .eva-order::before {
  border: 1px solid #e21b1a;
  border-radius: 2px;
}
.MyOrder .am-list-body {
  background-color: transparent;
}



