  margin-right: 5px;
  width: 85px;
  -ms-flex: initial;
      flex: initial;
}
.NewAddress .take-region .am-list-extra {
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 14px;
  text-align: left;
  color: #E0E0E0;
}
.NewAddress .save-address {
  height: 44px;
  line-height: 44px;
  font-size: 16px;
}



