.Vanity.base-content {
  height: calc(100vh - 88.5px);
}
.Vanity .Carousel_Img {
  background: #fff;
}
.Vanity .vanity-num .am-tabs-default-bar-tab {
  font-size: 13px;
  color: #666;
}
.Vanity .vanity-num .am-tabs-default-bar-tab-active {
  color: #e21b1a;
}
.Vanity .vanity-num .am-tabs-default-bar {
  background: #fff !important;
}
.Vanity .vanity-num-list {
  padding: 10px;
}
.Vanity .vanity-num-list .listItem {
  margin-bottom: 10px;
  width: calc(50% - 5px);
  background: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: center;
}
.Vanity .vanity-num-list .listItem:nth-child(2n) {
  margin-left: 10px;
}
.Vanity .vanity-num-list .listItem em {
  color: #e21b1a;
}
.Vanity .vanity-num-list .listItem-num {
  padding: 10px 0;
  line-height: 33px;
  font-size: 24px;
  color: #2a2a2a;
}
.Vanity .vanity-num-list .listItem-price {
  padding: 5px 0;
  line-height: 17px;
  background: #fff;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  font-size: 12px;
  color: #999;
}



