.ApplyInvoice .applyCheck {
  margin-top: 15px;
  background: #fff;
}
.ApplyInvoice .applyCheck .discount-item {
  padding: 13px 15px;
  background: #fff;
}
.ApplyInvoice .applyCheck .discount-item .am-flexbox-item {
  font-size: 13px;
}
.ApplyInvoice .applyCheck .discount-item .am-flexbox-item em {
  margin-left: 16px;
  color: #999;
}
.ApplyInvoice .applyCheck .discount-item .am-flexbox-item .am-list-line {
  padding-right: 0;
}
.ApplyInvoice .applyCheck .discount-item .am-radio {
  margin-top: -3px;
  width: 20px;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 50%;
}
.ApplyInvoice .applyCheck .discount-item .am-radio .am-radio-inner {
  width: 20px;
  height: 20px;
  border-width: 1px;
}
.ApplyInvoice .applyCheck .discount-item .am-radio.am-radio-checked .am-radio-inner::after {
  width: 10px;
  height: 10px;
  background: #e21b1a;
  border-radius: 50%;
  top: 50% !important;
  right: 50% !important;
  border-color: #e21b1a;
  border-width: inherit;
  -webkit-transform: translate(50%, -50%);
      -ms-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
}
.ApplyInvoice .applyCheck .discount-item .am-list-item {
  padding-left: 0;
  height: auto;
  min-height: auto;
  -ms-flex: 1 1;
      flex: 1 1;
}
.ApplyInvoice .applyCheck .discount-item .am-list-item .am-list-line {
  padding-left: 0;
}
.ApplyInvoice .applyCheck .discount-item .am-list-item .am-input-label {
  font-size: 13px;
  color: #333;
}
.ApplyInvoice .applyCheck .discount-item .am-list-item input {
  padding: 1px 0;
  font-size: 14px;
}
.ApplyInvoice .applyCheck .discount-item .am-list-item ::-webkit-input-placeholder {
  font-size: 14px;
  color: #e0e0e0;
}
.ApplyInvoice .applyCheck .discount-item .am-checkbox-wrapper {
  margin-left: 8px;
}
.ApplyInvoice .applyCheck .discount-item .leftFlex {
  width: 70px;
  -ms-flex: inherit;
      flex: inherit;
}
.ApplyInvoice-opear {
  width: 100%;
  padding: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  left: 0;
  bottom: 0;
}
.ApplyInvoice-opear .am-button {
  height: 44px;
  line-height: 44px;
  font-size: 14px;
}
.ApplyInvoice-opear .am-button.payBtn {
  margin-right: 80px;
  border: 1px solid #e21b1a;
  color: #e21b1a;
}
.ApplyInvoice-opear .am-button.payBtn::before {
  border: none;
}



