.Pay .base-content {
  padding: 15px;
}
.Pay-box {
  background: #fff;
  border-radius: 8px;
}
.Pay-box-goods {
  padding: 20px 15px;
}
.Pay-box-goods .goodsImg {
  width: 66px;
  height: 66px;
}
.Pay-box-goods .goodsImg img {
  width: 100%;
  display: block;
}
.Pay-box-mode {
  padding: 20px 15px;
  background: #fbfbfb;
}
.Pay-box-mode .pay-check {
  margin-top: 23px;
  position: relative;
  padding-bottom: 35px;
}
.Pay-box-mode .pay-check .am-flexbox {
  margin-bottom: 19px;
}
.Pay-box-mode .pay-check .am-flexbox:last-of-type {
  margin-bottom: 0;
}
.Pay-box-mode .pay-check .am-flexbox-item {
  font-size: 13px;
}
.Pay-box-mode .pay-check .am-flexbox-item:last-of-type {
  -ms-flex: inherit;
      flex: inherit;
  text-align: right;
}
.Pay-box-mode .pay-check .am-flexbox-item img {
  height: 22px;
}
.Pay-box-mode .pay-check .am-flexbox-item span {
  line-height: 13px;
}
.Pay-box-mode .pay-check .am-flexbox-item .pay-gold {
  letter-spacing: 0;
  color: #404143;
}
.Pay-box-mode .pay-check .am-flexbox-item .account-money {
  margin-left: 10px;
  color: #999;
}
.Pay-box-mode .pay-check .am-radio {
  padding: 2.5px;
  border: 1px solid #ccc;
  border-radius: 50%;
}
.Pay-box-mode .pay-check .am-radio.am-radio-checked {
  border: 1px solid #e21b1a;
  background: #e21b1a;
}
.Pay-box-mode .pay-check .am-radio.am-radio-checked .am-radio-inner::after {
  width: 5px;
  height: 10px;
  top: -1px;
  right: 7px;
  border-color: #fff;
}
.Pay-box-mode .pay-check .payMoney {
  color: #e21b1a;
  margin-top: 20px;
  margin-right: 10px;
  position: absolute;
  right: 0;
  font-weight: 600;
  font-size: 18px;
}
.Pay-box-mode .pay-check .payMoney em {
  color: #999;
  font-weight: normal;
  font-size: 12px;
}
.Pay-box-opear {
  padding: 30px 15px;
}
.Pay-box-opear .am-button {
  width: 100%;
  height: 40px;
  line-height: 40px;
}
.Pay-box-opear .am-button span {
  font-size: 16px;
}



