.recommend-goodsItem {
  margin-bottom: 10px;
  width: calc(50% - 5px);
}
.recommend-goodsItem.sale-item {
  background: #fff;
}
.recommend-goodsItem:nth-child(2n) {
  margin-left: 10px;
}
.recommend-goodsItem-img img {
  width: 100%;
  display: block;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.recommend-goodsItem-body {
  padding: 10px 15px;
  background: #f7f7f7;
  -webkit-box-shadow: inset 0 0 0 0 #f0f0f0;
          box-shadow: inset 0 0 0 0 #f0f0f0;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.recommend-goodsItem-title {
  margin-bottom: 5px;
  line-height: 17px;
  height: 36px;
  font-size: 13px;
  color: #333;
}
.recommend-goodsItem .goodsPrice {
  font-size: 16px;
  color: #333;
}
.recommend-goodsItem .am-button {
  position: unset !important;
}
.recommend-goodsItem .sale-num {
  font-size: 14px;
  color: #333;
  font-weight: 500;
}



