.SalesLog .salesBox {
  padding: 15px;
}
.SalesLog .salesBoxItem {
  position: relative;
  padding: 15px 10px 12px;
  background: #fff;
  border-radius: 4px;
}
.SalesLog .salesBoxItem .salesTotal {
  width: 85px;
  height: 85px;
  line-height: 85px;
  border-radius: 50%;
  border: 4px solid #E31B19;
  text-align: center;
  font-size: 24px;
  color: #333;
}
.SalesLog .salesBoxItem .salesTit {
  margin-top: 11px;
  font-size: 15px;
  color: #181818;
}
.SalesLog .salesBoxItem .salesTime {
  position: absolute;
  left: 10px;
  top: 5px;
}
.SalesLog .salesBoxItem .salesTime .am-list-body::before {
  height: 0;
}
.SalesLog .salesBoxItem .salesTime .am-list-body .am-list-item {
  padding-left: 0;
}
.SalesLog .salesBoxItem .salesTime select {
  font-size: 12px;
  color: #181818;
}



