.EvaluateOrder .evaluateBox {
  padding: 15px;
  background: #fff;
}
.EvaluateOrder .evaluateBox .am-textarea-item {
  padding-left: 0;
}
.EvaluateOrder .evaluateBox .am-textarea-item textarea {
  min-height: 64px;
  font-size: 14px;
  color: #333;
}
.EvaluateOrder .evaluateBox .am-textarea-item ::-webkit-placeholder-input {
  font-size: 14px;
}
.EvaluateOrder .evaluateBox .anonymity {
  text-align: right;
}
.EvaluateOrder .evaluateBox .anonymity .am-checkbox-agree-label {
  margin-top: 5px;
  font-size: 13px;
  color: #e21b1a;
}



