.UserCenter .userInfo {
  height: 139px;
  padding: 17px;
  background-color: #fff;
  background-size: 100% 100%;
  background-position: 20%;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
.UserCenter .userInfo .uImg {
  width: 46px;
  height: 46px;
  border-radius: 50%;
}
.UserCenter .userInfo .userInfoName {
  margin-left: 11px;
  font-size: 14px;
  color: #666;
}
.UserCenter .userInfo .userInfoId {
  margin-left: 11px;
  font-size: 12px;
  color: #666;
  margin-top: 10px;
}
.UserCenter .uOrderItem {
  margin-top: -55px;
  padding: 15px;
  background: #fff;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}
.UserCenter .uOrderItem.mt0 {
  margin-top: 0;
}
.UserCenter .uOrderItem .shopMenu {
  text-align: center;
}
.UserCenter .uOrderItem .shopMenu .am-list-body::before,
.UserCenter .uOrderItem .shopMenu .am-list-body::after {
  height: 0;
}
.UserCenter .uOrderItem .shopMenu .am-list-body .am-list-item {
  padding-left: 0;
  height: 30px;
  min-height: 30px;
}
.UserCenter .uOrderItem .shopMenu .am-list-body .am-list-item .am-list-line {
  padding-right: 0;
}
.UserCenter .uOrderItem .shopMenu .am-list-body .am-list-item .am-list-line::after {
  height: 0;
}
.UserCenter .uOrderItem .shopMenu .am-list-body .am-list-item .am-list-line .am-list-content {
  padding-top: 0;
  font-size: 16px;
  color: #404143;
}
.UserCenter .uOrderItem .shopMenu .am-list-body .am-list-item .am-list-line .am-list-extra {
  padding-top: 0;
  font-size: 13px;
  color: #999;
}
.UserCenter .uOrderItem .shopMenu .am-list-body .am-list-item .am-list-line .am-list-arrow-horizontal {
  margin-top: -7px;
}
.UserCenter .uOrderItem .shopMenu .uIco {
  margin-bottom: 10px;
  width: 24px;
  height: 25px;
}
.UserCenter .uOrderItem .shopMenu .uTit {
  font-size: 12px;
  color: #999;
}
.UserCenter .uOrderItem .shopMenu .menuIco {
  margin-top: 23px;
  overflow: visible;
}
.UserCenter .uOrderItem .shopMenu .menuIco .am-badge-text {
  top: -35px;
}
.UserCenter .shopCart {
  font-size: 16px;
  color: #404143;
}
.UserCenter .baseItem .am-list-item {
  padding-left: 0;
}
.UserCenter .baseItem .am-list-item .am-list-line {
  padding-left: 15px;
}
.UserCenter .baseItem .am-list-item .am-list-line .am-list-content {
  font-size: 14px;
  color: #333;
}
.UserCenter .baseItem .am-list-extra {
  font-size: 14px;
}



