.Share {
  width: 100%;
  height: 100%;
}
.Share .content {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
}
.Share .middle-con {
  position: absolute;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.Share .operation-con {
  position: absolute;
  bottom: 40px;
  padding: 0 40px;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.Share .operation-con .android,
.Share .operation-con .ios {
  background: #fff;
  border: 1px solid #e25a5e;
  color: #e25a5e;
}
.Share .operation-con .android img,
.Share .operation-con .ios img {
  width: 25px;
  height: 25px;
  vertical-align: text-top;
  margin-right: 10px;
}
.Share .operation-con .integral-con {
  border-radius: 5px;
  border: 1px solid #e25a5e;
  background: #fff;
  padding: 10px 20px;
  margin-bottom: 20px;
}
.Share .operation-con .integral-con .icon-con {
  margin-bottom: 10px;
}
.Share .operation-con .integral-con .icon-con img {
  width: 40px;
}
.Share .operation-con .integral-con .icon-con .tips-con {
  margin-left: 10px;
}
.Share .operation-con .integral-con .icon-con .tips-con .title {
  font-size: 13px;
  color: #333;
  margin-bottom: 5px;
}
.Share .operation-con .integral-con .icon-con .tips-con .tips {
  font-size: 13px;
  color: #e25a5e;
}
.Share .operation-con .integral-con .receive-con {
  border: 1px solid #e25a5e;
  line-height: 36px;
}
.Share .operation-con .integral-con .receive-con input {
  height: 36px;
  border: 0;
  padding: 0 10px;
  font-size: 13px;
}
.Share .operation-con .integral-con .receive-con .receive {
  background: #e25a5e;
  color: #fff;
  height: 36px;
  width: 100px;
  text-align: center;
}



