.ExLog .exBank {
  padding: 15px;
  background: #fff;
}
.ExLog .exBank .exCarImg {
  margin-right: 15px;
  width: 36px;
  height: 36px;
}
.ExLog .exBank .exInfo {
  font-size: 12px;
  color: #151515;
}
.ExLog .exBank .exInfoItem {
  margin-bottom: 10px;
}
.ExLog .exBank .exInfoItem:last-of-type {
  margin-bottom: 0;
}
.ExLog .exBank .exInfoItem span {
  color: #999;
}
.ExLog .exStep {
  padding: 15px;
  background: #fff;
}
.ExLog .exStep .am-steps-item-icon {
  position: relative;
}
.ExLog .exStep .am-steps-item-icon::before {
  content: "";
  width: 5px;
  height: 5px;
  background: #999;
  border-radius: 50%;
  position: absolute;
  left: 7px;
  top: 5px;
}
.ExLog .exStep .am-steps-item-icon .am-icon {
  display: none;
}
.ExLog .exStep .am-steps-item-title {
  margin-bottom: 10px;
  font-size: 12px;
  color: #333;
}
.ExLog .exStep .am-steps-item-title div {
  line-height: 18px;
}
.ExLog .exStep .am-steps-item-title a {
  color: #518ee8;
}
.ExLog .exStep .last .am-steps-item-title,
.ExLog .exStep .last .am-steps-item-description {
  color: #e21b1a;
}
.ExLog .exStep .last .am-steps-item-icon::before {
  background: #e21b1a;
}



