.H5Album {
  background: #fff;
  padding-bottom: 30px;
}
.H5Album .bg-con {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding-left: 40px;
  padding-right: 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.H5Album .bg-con .cover-image {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 90px;
  height: 90px;
  position: relative;
  border-radius: 3px;
}
.H5Album .bg-con .cover-image img {
  width: 40px;
  height: 40px;
}
.H5Album .bg-con .cover-image .play-num-con {
  position: absolute;
  bottom: 0px;
  right: 0px;
  border-radius: 3px;
  padding: 0px 4px;
  color: #fff;
  background: rgba(0, 0, 0, 0.3);
  font-size: 10px;
}
.H5Album .bg-con .cover-image .play-num-con img {
  width: 20px;
  height: 20px;
}
.H5Album .bg-con .title {
  font-size: 20px;
  color: #fff;
  margin-top: 15px;
  text-align: center;
}
.H5Album .bg-con .audio-player-con {
  position: relative;
  height: 30px;
  width: 94vw;
}
.H5Album .bg-con .audio-player-con .time {
  color: transparent;
  font-size: 14px;
  width: 100%;
}
.H5Album .bg-con .audio-player-con .time .current-time {
  float: left;
  color: #fff;
}
.H5Album .bg-con .audio-player-con .time .total-time {
  float: right;
  color: #fff;
}
.H5Album .bg-con .audio-player-con .audio-player {
  position: absolute;
  width: 100%;
}
.H5Album .bg-con .audio-player-con .audio-player > div {
  background: transparent !important;
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  height: 50px !important;
}
.H5Album .bg-con .audio-player-con .audio-player .toggle-play-wrapper {
  display: none;
}
.H5Album .bg-con .audio-player-con .audio-player .progress-bar-wrapper {
  width: 100%;
  padding: 0 !important;
}
.H5Album .bg-con .audio-player-con .audio-player .progress-bar-wrapper div:first-of-type {
  height: 3px !important;
  opacity: 0.5;
}
.H5Album .bg-con .audio-player-con .audio-player .progress-bar-wrapper .indicator {
  top: -8px !important;
}
.H5Album .bg-con .audio-player-con .audio-player .volume-controls {
  display: none;
}
.H5Album .bg-con .mobi-input {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 3px;
  padding: 8px 10px;
  margin-top: 30px;
  text-align: center;
  border-radius: 20px;
  width: 60%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 40px;
}
.H5Album .bg-con .open-btn {
  margin-top: 15px;
  border: 0;
  line-height: 40px;
  height: 40px;
  padding-left: 22px;
  padding-right: 22px;
  border-radius: 20px;
  font-size: 14px;
  width: 60%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.H5Album .bg-con .open-btn::before {
  height: 0;
  border: 0;
}
.H5Album .user-info {
  padding: 15px;
  border-bottom: 1px solid #eee;
}
.H5Album .user-info .header {
  width: 50px;
  height: 50px;
  border-radius: 5px;
  margin-right: 10px;
}
.H5Album .user-info .title {
  font-size: 14px;
  color: #000;
  margin-bottom: 10px;
}
.H5Album .user-info .num-con {
  font-size: 12px;
  color: #999;
}
.H5Album .user-info .num-con .num {
  margin-left: 30px;
}
.H5Album .audio-list-con {
  padding-left: 15px;
}
.H5Album .audio-list-con .item-con {
  border-bottom: 1px solid #eee;
  font-size: 13px;
  color: #333;
  padding: 12px 10px 12px 0;
  line-height: 18px;
}
.H5Album .audio-list-con .item-con img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.H5Album .more {
  padding: 15px;
  text-align: center;
  font-size: 13px;
  color: #f86442;
  padding-bottom: 30px;
  font-weight: 500;
}
.H5Album .comment-con {
  padding: 20px 10px;
}
.H5Album .comment-con .title {
  font-size: 13px;
  color: #333;
  font-weight: 500;
  padding: 15px;
}
.H5Album .comment-con .comment-item {
  padding: 10px 15px;
  border-bottom: 1px solid #eee;
}
.H5Album .comment-con .comment-item img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}
.H5Album .comment-con .comment-item .comment-info .name {
  font-size: 13px;
  color: #333;
  margin-bottom: 5px;
}
.H5Album .comment-con .comment-item .comment-info .content {
  font-size: 13px;
  color: #999;
}
.H5Album .nodata {
  padding: 15px;
  color: #999;
  font-size: 13px;
  text-align: center;
}



